import React, { useState } from "react";
import { withRouter } from "react-router-dom";
import styled from "styled-components";
import Header from "../../Common/Header";
import Footer from "../../Common/Footer";
import StyledBox from "../../Style/styledBox";
import MyPageTitle from "../../Style/MyPageTitle";
import MyPageInput from "../../Style/MyPageInput";
import MyPageButton from "../../Style/MyPageButton";
import axiosInstance, { endpoints } from "../../../axios";

const Titlebox = styled.div`
  display: flex;
  justify-content: space-between;
`;

const Subtitle = styled.p`
  color: #ccc;
  font-size: 12px;
  font-weight: normal;
  line-height: 24px;
  padding-right: 4px;
  margin-top: 10px;
`;

const Warning = styled.strong`
  color: #c62917;
  margin-left: 4px;
  font-weight: normal;
`;

const Alert = styled.p`
  color: #999;
  font-size: 12px;
  line-height: 18px;
  margin-top: 20px;
`;

function Password({ history }) {
  const [inputs, setInput] = useState({
    currentPassword: "",
    checkPassword: "",
    newPassword: "",
  });
  const { currentPassword, checkPassword, newPassword } = inputs;
  const userFrom = localStorage.getItem("userId");

  const onChangeHandler = (e) => {
    const { value, name } = e.currentTarget;
    setInput({
      ...inputs,
      [name]: value,
    });
  };

  const onSubmitHandler = (e) => {
    e.preventDefault();
    let body = {
      _id: userFrom,
      oldPassword: currentPassword,
      newPassword: newPassword,
    };
    if (newPassword !== checkPassword) {
      alert("  .");
    } else {
      axiosInstance
        .put(endpoints.user.updatePassword, body)
        .then((response) => {
          if (!response.data.changeSuccess) {
            alert(response.data.message);
          } else {
            if (response.data.changeSuccess) {
              history.push("/mypage");
              alert(" .");
            } else {
              alert("  .");
            }
          }
        });
    }
  };

  return (
    <>
      <Header title=" " link="/board" backbutton={true} />
      <form onSubmit={onSubmitHandler}>
        <StyledBox>
          <Titlebox>
            <MyPageTitle> </MyPageTitle>
            <Subtitle>4~20</Subtitle>
          </Titlebox>
          <MyPageInput
            type="password"
            name="newPassword"
            placeholder=" "
            value={newPassword}
            onChange={onChangeHandler}
          />
          <MyPageInput
            type="password"
            name="checkPassword"
            placeholder="  "
            value={checkPassword}
            onChange={onChangeHandler}
          />
          <MyPageTitle> </MyPageTitle>
          <MyPageInput
            type="password"
            name="currentPassword"
            placeholder=" "
            value={currentPassword}
            onChange={onChangeHandler}
          />
          <div>
            <Alert>
              <strong>     ?</strong>
              <br />
                  ,    
                .    ,  
                 
              <Warning>     .</Warning>
            </Alert>
            <Alert>
              <strong>     ?</strong>
              <br />
                 .  
              <Warning>    .</Warning>
            </Alert>
          </div>
          <MyPageButton> </MyPageButton>
        </StyledBox>
      </form>
      <Footer />
    </>
  );
}

export default withRouter(Password);
