import React, { useState } from "react";
import Header from "../components/Common/Header";
import StyledBox from "../components/Style/styledBox";
import StyledContainer from "../components/Style/styledContainer";
import CheckIdButton from "../components/Register/ChcekIdButton";
import RegisterInput from "../components/Register/RegisterInput";
import LimitOnLength from "../components/Register/LimitOnLength";
import RegisterButton from "../components/Register/RegisterButton";
import RegisterSelect from "../components/Register/RegisterSelect";
import SchoolSearchResult from "../components/Register/SchoolSearchResult";
import { useDispatch } from "react-redux";
import { registerUser } from "../_actions/user_actions";
import { withRouter } from "react-router-dom";
import axiosInstance, { endpoints } from "../axios";

const SCHOOL_ARR = [
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
].sort();

const entranceYearArray = [];
for (let i = 2000; i < 2022; i++) {
  entranceYearArray.push(i);
}

function Register({ history }) {
  const dispatch = useDispatch();
  const [inputs, setInput] = useState({
    userId: "",
    userPw: "",
    userEmail: "",
    userNickname: "",
    usableId: false,
  });

  const { userId, userPw, userEmail, userNickname, usableId } = inputs;
  const [option, setOption] = useState("2021");
  const [schoolInput, setSchoolInput] = useState("");
  const [searchResult, setSearchResult] = useState(SCHOOL_ARR);
  const [showSchoolList, setShowSchoolList] = useState(true);
  const [overIdLength, setOverIdLength] = useState(false);
  const [overPwLength, setOverPwLength] = useState(false);

  const onChange = (e) => {
    const { value, name } = e.target;
    setInput({
      ...inputs,
      [name]: value,
      usableId: usableId,
    });

    if (inputs.userId.length > 8) {
      setOverIdLength(true);
    } else {
      setOverIdLength(false);
    }

    if (inputs.userPw.length > 12) {
      setOverPwLength(true);
    } else {
      setOverPwLength(false);
    }
  };

  const checkId = (e) => {
    e.preventDefault();
    if (overIdLength) {
      return;
    }
    axiosInstance
      .get(`${endpoints.auth.check}?username=${userId}`)
      .then((response) => {
        console.log(response);
        if (response.status === 200) {
          setInput({
            ...inputs,
            usableId: true,
          });
          alert(" .");
        }
      })
      .catch((error) => {
        console.log(error);
        alert("  ");
      });
  };

  const handleOption = (e) => {
    setOption(e.target.value);
  };

  const handleSearch = (e) => {
    setShowSchoolList(true);
    setSchoolInput(e.target.value);
    const result = SCHOOL_ARR.filter((school) => {
      return school.includes(e.target.value);
    });
    setSearchResult(result);
  };

  const handleSearchClick = (e) => {
    e.preventDefault();
    setSchoolInput(e.target.textContent);
    setShowSchoolList(false);
  };

  const SignUp = (e) => {
    e.preventDefault();
    let body = {
      username: userId,
      password: userPw,
      email: userEmail,
      nickname: userNickname,
      entranceYear: option,
      collegeName: schoolInput,
    };
    if (overIdLength || overPwLength) {
      return;
    } else if (!userId || !userPw || !userEmail || !userNickname) {
      alert("  ");
      return;
    } else if (!SCHOOL_ARR.includes(schoolInput)) {
      alert(" ");
      return;
    } else if (usableId === false) {
      alert("  ");
      return;
    } else {
      dispatch(registerUser(body))
        .then((response) => {
          if (response.payload.isSuccessful) {
            alert(" .");
            history.push("./");
          } else {
            alert(" .");
          }
        })
        .catch((error) => console.log(error));
    }
  };
  return (
    <StyledContainer>
      <div>
        <Header link={"./"} title="" backbutton={true} />
        <StyledBox padding="18px 16px" lineHeight="20px">
          <form onSubmit={checkId}>
            <RegisterInput
              labelName=""
              name="userId"
              type="text"
              placeholder=""
              onChange={onChange}
              value={userId}
            />
            {overIdLength && (
              <LimitOnLength> 8  </LimitOnLength>
            )}
            <CheckIdButton onClick={checkId}></CheckIdButton>
          </form>
          <form onSubmit={SignUp}>
            <RegisterInput
              labelName=""
              name="userPw"
              type="password"
              placeholder=""
              onChange={onChange}
              value={userPw}
            />
            {overPwLength && (
              <LimitOnLength> 12  </LimitOnLength>
            )}
            <RegisterInput
              labelName=""
              name="userEmail"
              type="email"
              placeholder=""
              onChange={onChange}
              value={userEmail}
            />
            <RegisterInput
              labelName=""
              name="userNickname"
              type="text"
              placeholder=""
              onChange={onChange}
              value={userNickname}
            />
            <RegisterSelect
              labelName=""
              handleOption={handleOption}
              option={option}
              dataArr={entranceYearArray}
            />
            <RegisterInput
              labelName=""
              name="userSchool"
              type="text"
              placeholder=" "
              onChange={handleSearch}
              value={schoolInput}
            />

            {schoolInput && showSchoolList && (
              <SchoolSearchResult
                datas={searchResult}
                handleSearchClick={handleSearchClick}
              />
            )}
            <RegisterButton type="submit"></RegisterButton>
          </form>
        </StyledBox>
      </div>
    </StyledContainer>
  );
}

export default withRouter(Register);
